import React, { useState } from "react"
import { useStaticQuery, graphql } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import MenuItem from "../../components/menuitem.js"

import { Icon, Row, Col } from "react-materialize"

const ALaKarte_Menu_Page = () => {
  const images = useStaticQuery(graphql`
    query {
      cold_soba: file(relativePath: { eq: "noodles/soba/cold_soba.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  const [numTotalItems, setItems] = useState(0)

  const addItem = () => {
    setItems(numTotalItems + 1)
  }

  const removeItem = () => {
    setItems(numTotalItems - 1)
  }

  return (
    <Layout>
      <SEO title="Noodles | Online Ordering" />
      <div style={{ marginTop: "4%", marginLeft: "10%", width: "80%" }}>
        <Row>
          <Col s={3}>
            <MenuItem
              key={"1"}
              title="Cold Soba (cool)"
              price={5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_soba.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"2"}
              title="Cold Soba (cool)"
              price={5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_soba.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"3"}
              title="Cold Soba (cool)"
              price={5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_soba.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"4"}
              title="Cold Soba (cool)"
              price={5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_soba.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"5"}
              title="Cold Soba (cool)"
              price={5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_soba.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"6"}
              title="Cold Soba (cool)"
              price={5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_soba.childImageSharp.fluid}
            />
          </Col>
        </Row>
      </div>
    </Layout>
  )
}

export default ALaKarte_Menu_Page
